// import WalletConnect from "@walletconnect/client";
const loader = document.getElementById('loader');
const navItems = document.querySelectorAll('.nav-section');


function navigateTo(el) {
    // this makes the swap modal visible again - in case someone clicked on settings & then changed windows (THIS BEHAVIOUR APPLIES TO ALL MODALS)
    // LEFT FOR NOW - but to implement - I need to disable the checked state to make sure the settings modal hides 
    // if (el === 'swap') {
    //     hideSwap('#swap');
    // }
    const itemToShow = document.getElementById(el);
    navItems.forEach(item => {
        if (!item.classList.contains('hidden')) item.classList.add('hidden');
    });
    loader.classList.remove('hidden')

    setTimeout(() => {
        loader.classList.add('hidden');
    }, 500);

    itemToShow.classList.add('z-20');
    itemToShow.classList.remove('hidden');
}





function hideSwap(elId, hide) {
    const el = document.querySelector(elId);
    el.style.visibility = hide ? 'hidden' : 'visible';
}




/**
 * Redirect to BuyIbat section after page is done loading and the URL is referrer URL (const URL_Prefix = 'https://battleinfinstg.wpengine.com/BattleTeam/dapp.php?refercode=')
 * 
 */
function redirectToBuyIbatOnReferURL() {
    const URL_Prefix = 'https://battleinfinstg.wpengine.com/BattleTeam/dapp.php?refercode='

    let currentUrl = window.location.href;
    if (currentUrl.includes('refercode')) {
        console.info(`ReferUrl detected - navigating to buyIbat section`);
        navigateTo('arena');
    }

}
window.onload = redirectToBuyIbatOnReferURL;